button{
    width: 250px;
    height: 80px;
    font-family: Roboto, sans-serif;
    font-weight: bold;
    font-size: 35px;
    color: white;
    border: 2px solid #fff;
    border-radius: 15px;
    margin: 5px;
    cursor: pointer;
}

button:hover{
    background-color: #00471b;
}
.boton-click{
    background-color: #002ead;

}

.boton-reiniciar{
    background-color: #5a01a7;
}